.feedback-form {
  max-width: 480px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .12);
  border-radius: 4px;
  flex-direction: column;
  margin: auto;
  padding: 12px;
  display: flex;
  box-shadow: 0 -5px 15px rgba(0, 0, 0, .5);
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
  box-shadow: 0 -5px 15px rgba(0, 0, 0, .5);
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  padding: 8px 12px;
  box-shadow: 0 -5px 15px rgba(0, 0, 0, .5);
}

input, textarea {
  font: inherit;
  padding: 8px;
}

textarea {
  resize: none;
}

